<template>
  <div id="staggered-list-demo">
    <input v-model="query" />
    <transition-group
      name="staggered-fade"
      tag="ul"
      v-bind:css="false"
      v-on:before-enter="beforeEnter"
      v-on:enter="enter"
      v-on:leave="leave"
    >
      <li
        v-for="(item, index) in computedList"
        v-bind:key="item.msg"
        v-bind:data-index="index"
      >
        {{ item.msg }}
      </li>
    </transition-group>
  </div>
</template>

<script>
import Velocity from "velocity-animate";
export default {
  data() {
    return {
      query: "",
      list: [
        { msg: "Bruce Lee" },
        { msg: "Jackie Chan" },
        { msg: "Chuck Norris" },
        { msg: "Jet Li" },
        { msg: "Kung Fury" },
      ],
    };
  },
  computed: {
    computedList() {
      var vm = this;
      return this.list.filter(function(item) {
        return item.msg.toLowerCase().indexOf(vm.query.toLowerCase()) !== -1;
      });
    },
  },
  methods: {
    beforeEnter(el) {
      el.style.opacity = 0;
      el.style.height = 0;
    },
    enter(el, done) {
      var delay = el.dataset.index * 150;
      setTimeout(function() {
        Velocity(el, { opacity: 1, height: "1.6em" }, { complete: done });
      }, delay);
    },
    leave(el, done) {
      var delay = el.dataset.index * 150;
      setTimeout(function() {
        Velocity(el, { opacity: 0, height: 0 }, { complete: done });
      }, delay);
    },
  },
};
</script>
<style scoped>
#staggered-list-demo input {
  border: 1px solid black;
}
input {
  padding-left: 0.125em;
  margin-bottom: calc(var(--basic-space) / 2);
}
ul {
  display: block;
  padding-left: calc(var(--basic-space) / 2);
}
li {
  list-style: none;
}
</style>
